* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

ul {
  list-style: none; }

a,
a:hover {
  text-decoration: none; }

input,
button,
textarea,
select {
  outline: none; }

a,
button {
  cursor: pointer;
  border: none;
  color: #fff; }

body {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 400;
  background-color: #EEEEEE;
  font-size: 16px; }

.title {
  font-family: 'Oswald', sans-serif;
  font-weight: 700;
  font-size: 3rem;
  line-height: 4rem;
  text-align: center;
  color: #fff;
  margin-bottom: 2.625rem; }

.subtitle {
  font-size: 1.5rem;
  line-height: 2rem;
  text-align: center;
  color: #fff;
  max-width: 26.625rem;
  margin: 0 auto; }

.container {
  padding: 0 20px;
  max-width: 58.75rem;
  margin: 0 auto;
  position: relative; }

#nav-menu-toggle {
  display: none; }
